import React, { useState } from "react";
import { TextField, Button } from "@material-ui/core";
import Dialog from "@material-ui/core/Dialog";
import JoinAlert from "../join/JoinAlert";
import DialogContent from "@material-ui/core/DialogContent";
import axios from "axios";
import "./Login.css";

function Login() {
  const [openDia, setOpenDia] = React.useState<boolean>(false);
  const handleToJoin = (e: React.MouseEvent<HTMLButtonElement>) => {
    window.location.replace("/join");
  };
  const [loginId, setLoginId] = useState<string>("");
  const [loginPw, setLoginPw] = useState<string>("");
  const [open, setOpen] = useState<boolean>(false);

  const handleClose = () => {
    setOpen(false);
  };

  const login = () => {
    axios
      .post("api/member/login", { memberId: loginId, memberPw: loginPw })
      .then((res) => {
        localStorage.setItem("token", "Bearer " + res.data.token.accessToken);
        window.location.replace("/");
      })
      .catch(() => {
        setOpen(true);
      });
  };

  const handleClickOpen = () => {
    setOpenDia(true);
  };

  const handleCloseDia = () => {
    setOpenDia(false);
  };

  return (
    <div>
      <text
        className="loginText"
        onClick={handleClickOpen}
        style={{ cursor: "poin" }}
      >
        Login
      </text>
      <Dialog
        open={openDia}
        onClose={handleCloseDia}
        aria-labelledby="form-dialog-title"
      >
        <DialogContent>
          <div>
            {open ? (
              <JoinAlert
                message="    "
                handleClose={handleClose}
                open={open}
              />
            ) : null}
            <table className="loginTable">
              <h1>LOGIN</h1>
              <tbody>
                <tr id="loginId">
                  <th></th>
                  <td>
                    <TextField
                      id="standard-basic"
                      onChange={(e) => {
                        setLoginId(e.target.value);
                      }}
                      onKeyPress={(e) => {
                        if (e.key === "Enter") {
                          login();
                        }
                      }}
                    />
                  </td>
                </tr>

                <tr id="loginPW">
                  <th></th>
                  <td>
                    <TextField
                      id="standard-basic"
                      type="password"
                      onChange={(e) => {
                        setLoginPw(e.target.value);
                      }}
                      onKeyPress={(e) => {
                        if (e.key === "Enter") {
                          login();
                        }
                      }}
                    />
                  </td>
                </tr>
              </tbody>
            </table>
            <div className="loginBtns">
              <div className="loginBtnsLeft">
                <Button onClick={handleToJoin}></Button>
              </div>
              <div className="loginBtnsRight">
                <Button color="primary" onClick={login}>
                  
                </Button>
                <Button onClick={handleCloseDia} color="secondary">
                  
                </Button>
              </div>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default Login;
