import React, { useState } from "react";
import { Link } from "react-router-dom";
import MyPageBodyRight from "./MyPageBodyRight";
import "./MyPageBody.css";

function MyPageBody() {
  const [menu, setMenu] = useState<number>(0);
  return (
    <div className="mypageBody">
      <div className="mypageBodyLeft">
        <h1> </h1>
        <hr />
        <p onClick={() => setMenu(0)}> </p>
        <p onClick={() => setMenu(1)}> </p>

        <br />

        <h1> </h1>
        <hr />
        <p onClick={() => setMenu(2)}>  </p>
        <p onClick={() => setMenu(3)}>  </p>
        <p onClick={() => setMenu(4)}> </p>

        <br />

        <h1></h1>
        <hr />

        <Link
          to={{
            pathname: "/service/id",
            state: {
              index: 1,
            },
          }}
          style={{ textDecoration: "none" }}
        >
          <p className="menuService"></p>
        </Link>
      </div>
      <MyPageBodyRight menu={menu} />
    </div>
  );
}

export default MyPageBody;
